
.test {
  margin-top: 60px;
  margin-left: 100px;
  margin-right: 600px;

  &__heading {
    font-size: 1.5rem;
    font-weight: 600;
  }

  &__rich-text{
      margin-top: 30px;
      margin-bottom: 30px;
      border: solid 2px red; 
  }

  /* Style the buttons that are used to open and close the accordion panel */
  .accordion {
    background-color: yellow;
    color: black;
    cursor: pointer;
    padding: 18px;
    width: 100%;
    text-align: left;
    border: none;
    outline: none;
    transition: 0.4s;
    margin-bottom: 10px;
    border: none;
    font-size: 1rem;
    font-weight: 600;
    border-radius: 10px;
    border: solid 2px red; 
  }

  /* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
  .active, 
  .accordion:hover,
  button:focus {
    background-color: orange;
    background: orange;
    border-radius: 10px;
    border: solid 2px green; 
  }

  /* Style the accordion panel. Note: hidden by default */
  .panel {
    padding: 0 18px;
    background-color:lightgrey;
    display: none;
    overflow: hidden;
    border-radius: 10px;
    border: solid 2px purple; 
  }

  .accordion:after {
    content: '\02795'; /* Unicode character for "plus" sign (+) */
    font-size: 13px;
    color: green;
    float: right;
    margin-left: 5px;
    border: solid 2px blue; 
  }

  .active:after {
    content: "\2796"; /* Unicode character for "minus" sign (-) */
    border: solid 2px brown; 
  }
  
}

